import axios from "axios";
import React, { Component } from "react";
import { Link, Redirect } from "react-router-dom";
import baseUrl from "../AppConfig";

class KlientaiList extends Component {
  state = {
    klientai: [],
  };

  async componentDidMount() {
    await axios.get(baseUrl + "/api/klientas").then((res) => {
      const klientai = res.data;
      this.setState({ klientai });
    });
  }

  handleDelete = async (id) => {
    const status = await (
      await axios.delete(baseUrl + "/api/klientas/" + id)
    ).status;
    if (status && window.location.reload());
  };

  render() {
    const { klientai } = this.state;
    console.log(klientai);
    return (
      <>
        <h1>Klientai</h1>

        <table class="table">
          <thead>
            <tr>
              <th scope="col">Vardas</th>
              <th scope="col">Pavarde</th>
              <th scope="col">Gimimo data</th>
              <th scope="col">Kliento tipas</th>
              <th scope="col">Inventoriaus</th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            {klientai.map((klientas) => (
              <tr>
                <td>{klientas.vardas}</td>
                <td>{klientas.pavarde}</td>
                <td>{klientas.gimimoData.substring(0, 10)}</td>
                <td>
                  {klientas.tipas === "IPRASTINIS" ? "Įprastinis" : "Lojalus"}
                </td>
                <td>{klientas.inventorius.length}</td>
                <td>
                  <button
                    className="btn btn-sm btn-danger"
                    onClick={() => this.handleDelete(klientas.id)}
                  >
                    Ištrinti
                  </button>
                  <Link to={"/klientas/" + klientas.id}>
                    <button className="btn btn-sm btn-secondary ml-1 mr-1">
                      Atnaujinti
                    </button>
                  </Link>
                  <Link to={`/klientas/${klientas.id}/info`}>
                    <button className="btn btn-sm btn-info">Informacija</button>
                  </Link>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </>
    );
  }
}

export default KlientaiList;
