import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import NavBar from "./components/NavBar";
import React from "react";
import { Route, Switch } from "react-router-dom";
import KlientasForm from "./components/KlientasForm";
import KlientaiList from "./components/KlientaiList";
import KlientasInfo from "./components/KlientasInfo";
import InventoriusForm from "./components/InventoriusForm";
import Ataskaita from "./components/Ataskaita";

function App() {
  return (
    <React.Fragment>
      <NavBar />
      <div className="background">
        <div className="container mt-5" style={{ backgroundColor: "white" }}>
          <Switch>
            <Route
              path={["ponaskikis-spring/ataskaita", "/ataskaita"]}
              component={Ataskaita}
            />
            <Route
              path={[
                "ponaskikis-spring/klientas/:id/info",
                "/klientas/:id/info",
              ]}
              component={KlientasInfo}
            />
            <Route
              path={["ponaskikis-spring/klientas/:id", "/klientas/:id"]}
              component={KlientasForm}
            />
            <Route
              path={["ponaskikis-spring/inventorius/:id", "/inventorius/:id"]}
              component={InventoriusForm}
            />
            <Route
              path={["ponaskikis-spring/klientas", "/klientas", "/"]}
              component={KlientaiList}
            />
          </Switch>
        </div>
      </div>
    </React.Fragment>
  );
}

export default App;
