import axios from "axios";
import React, { Component } from "react";
import baseUrl from "../AppConfig";

class Ataskaita extends Component {
  state = {
    ataskaita1: [],
    ataskaita2: [],
  };

  async componentDidMount() {
    await axios.get(baseUrl + "/api/ataskaita/1").then((res) => {
      console.log(res.data);
      const klientai = res.data;
      this.setState({ ataskaita1: klientai });
    });
    await axios.get(baseUrl + "/api/ataskaita/2").then((res) => {
      console.log(res.data);
      const klientai = res.data;
      this.setState({ ataskaita2: klientai });
    });
  }

  render() {
    const { ataskaita1, ataskaita2 } = this.state;
    return (
      <div>
        <h1>Ataskaita</h1>
        <div style={{ minHeight: "300px" }}>
          <h5>
            5 klientai su bendru didžiausiu registruotų inventoriaus svoriu:
          </h5>
          <div>
            <table class="table">
              <thead>
                <tr>
                  <th scope="col">Vardas</th>
                  <th scope="col">Pavarde</th>
                  <th scope="col">Gimimo data</th>
                  <th scope="col">Kliento tipas</th>
                  <th scope="col">Bendras svoris</th>
                  <th scope="col"></th>
                </tr>
              </thead>
              <tbody>
                {ataskaita1.map((klientas) => (
                  <tr>
                    <td>{klientas.vardas}</td>
                    <td>{klientas.pavarde}</td>
                    <td>{klientas.gimimoData.substring(0, 10)}</td>
                    <td>
                      {klientas.tipas === "IPRASTINIS"
                        ? "Įprastinis"
                        : "Lojalus"}
                    </td>
                    <td>
                      {klientas.inventorius.reduce((a, b) => a + b.svoris, 0)}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
        <div style={{ minHeight: "300px" }}>
          <h5>
            5 klientai su bendru didžiausiu registruotų vienetų skaičiumi:
          </h5>
          <div>
            <table class="table">
              <thead>
                <tr>
                  <th scope="col">Vardas</th>
                  <th scope="col">Pavarde</th>
                  <th scope="col">Gimimo data</th>
                  <th scope="col">Kliento tipas</th>
                  <th scope="col">Inventoriaus</th>
                  <th scope="col"></th>
                </tr>
              </thead>
              <tbody>
                {ataskaita2.map((klientas) => (
                  <tr>
                    <td>{klientas.vardas}</td>
                    <td>{klientas.pavarde}</td>
                    <td>{klientas.gimimoData.substring(0, 10)}</td>
                    <td>
                      {klientas.tipas === "IPRASTINIS"
                        ? "Įprastinis"
                        : "Lojalus"}
                    </td>
                    <td>{klientas.inventorius.length}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    );
  }
}

export default Ataskaita;
