import axios from "axios";
import React, { Component } from "react";
import baseUrl from "../AppConfig";
import { Link, Redirect } from "react-router-dom";

class KlientasInfo extends Component {
  state = {
    id: "",
    vardas: "",
    pavarde: "",
    gimimoData: "",
    tipas: "",
    inventorius: [],
  };

  async componentDidMount() {
    const id = this.props.match.params.id;
    await axios.get(baseUrl + "/api/klientas/" + id).then((res) => {
      this.setState({
        id: this.props.match.params.id,
        vardas: res.data.vardas,
        pavarde: res.data.pavarde,
        gimimoData: res.data.gimimoData
          ? res.data.gimimoData.toString().substring(0, 10)
          : "",
        tipas: res.data.tipas,
        inventorius: res.data.inventorius,
      });
    });
    console.log(this.state);
  }

  render() {
    const { id, vardas, pavarde, inventorius } = this.state;
    console.log(inventorius);
    return (
      <div className="m-5">
        <div className="row">
          <div className="col">
            <h1>
              {vardas} {pavarde}
            </h1>
          </div>
          <div className="col text-right">
            <Link to={`/inventorius/${id}`}>
              <button className="btn btn-lg btn-success">
                Prideti inventoriu
              </button>
            </Link>
          </div>
        </div>
        <table class="table">
          <thead>
            <tr>
              <th scope="col">Pavadinimas</th>
              <th scope="col">Svoris</th>
              <th scope="col">Talpinimo sektorius</th>
              <th scope="col">Patalpinimo data</th>
            </tr>
          </thead>
          <tbody>
            {inventorius.map((dalykas) => (
              <tr>
                <th>{dalykas.pavadinimas}</th>
                <td>{dalykas.svoris}</td>
                <td>{dalykas.talpinimoSektorius}</td>
                <td>{dalykas.data.substring(0, 10)}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  }
}

export default KlientasInfo;
