import axios from "axios";
import React, { Component } from "react";
import { Link } from "react-router-dom";
import baseUrl from "../AppConfig";

class KlientasForm extends Component {
  state = {
    id: "",
    vardas: "",
    pavarde: "",
    gimimoData: "",
    tipas: "",
  };

  async componentDidMount() {
    const id = this.props.match.params.id;
    if (id === 0) return;

    await axios.get(baseUrl + "/api/klientas/" + id).then((res) => {
      this.setState({
        id,
        vardas: res.data.vardas,
        pavarde: res.data.pavarde,
        gimimoData: res.data.gimimoData
          ? res.data.gimimoData.toString().substring(0, 10)
          : "",
        tipas: res.data.tipas,
      });
    });
    console.log(this.state);
  }

  handleChange = (e) => {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  };

  handleSubmit = (e) => {
    e.preventDefault();
    const { id, vardas, pavarde, gimimoData, tipas } = this.state;
    console.log(id, vardas, pavarde, gimimoData, tipas);
    if (id < 1) {
      axios
        .post(baseUrl + "/api/klientas/", {
          vardas,
          pavarde,
          gimimoData,
          tipas,
        })
        .then(() => this.props.history.push("/"));
    } else {
      axios
        .put(baseUrl + "/api/klientas/" + id, {
          vardas,
          pavarde,
          gimimoData,
          tipas,
        })
        .then(() => this.props.history.push("/"));
    }
  };

  render() {
    const { vardas, pavarde, gimimoData, tipas } = this.state;

    return (
      <div className="m-5">
        <form onSubmit={(e) => this.handleSubmit(e)}>
          <div className="form-group">
            <label>Vardas</label>
            <input
              name="vardas"
              onChange={(e) => this.handleChange(e)}
              defaultValue={vardas}
              type="text"
              className="form-control"
              id="vardas"
            />
          </div>
          <div className="form-group">
            <label>Pavarde</label>
            <input
              name="pavarde"
              onChange={(e) => this.handleChange(e)}
              defaultValue={pavarde}
              type="text"
              className="form-control"
              id="pavarde"
            />
          </div>
          <div className="form-group">
            <label>Gimimo data</label>
            <input
              name="gimimoData"
              onChange={(e) => this.handleChange(e)}
              defaultValue={gimimoData}
              type="date"
              className="form-control"
              id="gimimoData"
            />
          </div>
          <div class="form-group">
            <label>Kliento tipas</label>
            <select
              name="tipas"
              onChange={this.handleChange}
              class="form-control"
              id="exampleFormControlSelect1"
            >
              <option value="IPRASTINIS">Įprastinis</option>
              <option value="LOJALUSKLIENTAS">Lojalus</option>
            </select>
          </div>
          <button type="submit" className="btn btn-primary">
            Patvirtinti
          </button>
          <Link to="/" className="btn btn-danger btn-sm m-3">
            Atgal
          </Link>
        </form>
      </div>
    );
  }
}

export default KlientasForm;
