import axios from "axios";
import React, { Component } from "react";
import { Link } from "react-router-dom";
import baseUrl from "../AppConfig";

class InventoriusForm extends Component {
  state = {
    id: 0,
    pavadinimas: "",
    svoris: "",
    talpinimoSektorius: "",
    data: "",
  };
  async componentDidMount() {
    await axios.get(baseUrl + "/api/inventorius/").then((res) =>
      this.setState({
        id: this.props.match.params.id,
        pavadinimas: res.data.pavadinimas,
        svoris: res.data.svoris,
        talpinimoSektorius: res.data.talpinimoSektorius,
        data: res.data.data,
      })
    );
    console.log(this.state);
  }

  handleChange = (e) => {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  };

  handleSubmit = (e) => {
    e.preventDefault();
    const { id, pavadinimas, svoris, talpinimoSektorius, data } = this.state;
    axios
      .post(baseUrl + "/api/inventorius", {
        pavadinimas,
        svoris,
        talpinimoSektorius,
        data,
        klientasId: id,
      })
      .then(() => this.props.history.push("/"));
  };

  render() {
    const { pavadinimas, svoris, talpinimoSektorius, data } = this.state;

    return (
      <div className="m-5">
        <form onSubmit={(e) => this.handleSubmit(e)}>
          <div className="form-group">
            <label>Pavaidinimas</label>
            <input
              name="pavadinimas"
              onChange={(e) => this.handleChange(e)}
              defaultValue={pavadinimas}
              type="text"
              className="form-control"
              id="pavadinimas"
            />
          </div>
          <div className="form-group">
            <label>Svoris</label>
            <input
              name="svoris"
              onChange={(e) => this.handleChange(e)}
              defaultValue={svoris}
              type="text"
              className="form-control"
              id="svoris"
            />
          </div>
          <div className="form-group">
            <label>Talpinimo Sektorius</label>
            <input
              name="talpinimoSektorius"
              onChange={(e) => this.handleChange(e)}
              defaultValue={talpinimoSektorius}
              type="number"
              min={0}
              max={40}
              className="form-control"
              id="talpinimoSektorius"
            />
          </div>
          <div className="form-group">
            <label>Data</label>
            <input
              name="data"
              onChange={(e) => this.handleChange(e)}
              defaultValue={data}
              type="date"
              className="form-control"
              id="data"
            />
          </div>
          <button type="submit" className="btn btn-primary">
            Patvirtinti
          </button>
          <Link to="/" className="btn btn-danger btn-sm m-3">
            Atgal
          </Link>
        </form>
      </div>
    );
  }
}

export default InventoriusForm;
